// Socket.IO Client for Real-time Features

import { io, Socket } from 'socket.io-client';
import { getToken } from '@/lib/auth/authHelpers';

const SOCKET_URL = process.env.NEXT_PUBLIC_API_URL?.replace('/api', '') || 'http://localhost:5000';

let socket: Socket | null = null;

export interface SocketEventHandlers {
  onConnect?: () => void;
  onDisconnect?: () => void;
  onError?: (error: { message: string }) => void;
  
  // Post events
  onPostLiked?: (data: { postId: string; userId: string; liked: boolean; likesCount: number }) => void;
  
  // Comment events
  onCommentCreated?: (data: { postId: string; comment: any; commentsCount: number }) => void;
  onCommentLiked?: (data: { commentId: string; userId: string; liked: boolean; likesCount: number }) => void;
  
  // Poll events
  onPollUpdated?: (data: { postId: string; pollOptions: any[]; voterId: string }) => void;
  
  // Notification events
  onNotificationComment?: (data: any) => void;
  onNotificationMention?: (data: any) => void;
  onNotificationLike?: (data: any) => void;
  onNotificationCommentLike?: (data: any) => void;
  
  // User events
  onUserOnline?: (data: { userId: string }) => void;
  onUserOffline?: (data: { userId: string }) => void;
  onUserLocationChanged?: (data: { userId: string; city: string; state: string; country: string }) => void;
  
  // Location request
  onLocationRequest?: (data: { message: string; timestamp: Date }) => void;
}

/**
 * Initialize Socket.IO connection
 */
export function initializeSocket(handlers?: SocketEventHandlers): Socket {
  if (socket?.connected) {
    return socket;
  }
  
  const token = getToken();
  
  socket = io(SOCKET_URL, {
    auth: { token },
    transports: ['websocket', 'polling'],
    reconnection: true,
    reconnectionAttempts: 5,
    reconnectionDelay: 1000,
  });
  
  // Connection events
  socket.on('connect', () => {
    console.log('âœ… Socket connected:', socket?.id);
    handlers?.onConnect?.();
  });
  
  socket.on('disconnect', () => {
    console.log('âŒ Socket disconnected');
    handlers?.onDisconnect?.();
  });
  
  socket.on('error', (error: { message: string }) => {
    console.error('Socket error:', error);
    handlers?.onError?.(error);
  });
  
  // Post events
  socket.on('post:liked', (data) => {
    handlers?.onPostLiked?.(data);
  });
  
  // Comment events
  socket.on('comment:created', (data) => {
    handlers?.onCommentCreated?.(data);
  });
  
  socket.on('comment:liked', (data) => {
    handlers?.onCommentLiked?.(data);
  });
  
  // Poll events
  socket.on('poll:updated', (data) => {
    handlers?.onPollUpdated?.(data);
  });
  
  // Notification events
  socket.on('notification:comment', (data) => {
    handlers?.onNotificationComment?.(data);
  });
  
  socket.on('notification:mention', (data) => {
    handlers?.onNotificationMention?.(data);
  });
  
  socket.on('notification:like', (data) => {
    handlers?.onNotificationLike?.(data);
  });
  
  socket.on('notification:comment_like', (data) => {
    handlers?.onNotificationCommentLike?.(data);
  });
  
  // User events
  socket.on('user:online', (data) => {
    handlers?.onUserOnline?.(data);
  });
  
  socket.on('user:offline', (data) => {
    handlers?.onUserOffline?.(data);
  });
  
  socket.on('user:location_changed', (data) => {
    handlers?.onUserLocationChanged?.(data);
  });
  
  // Location request
  socket.on('location:request', (data) => {
    handlers?.onLocationRequest?.(data);
  });
  
  return socket;
}

/**
 * Get the current socket instance
 */
export function getSocket(): Socket | null {
  return socket;
}

/**
 * Disconnect socket
 */
export function disconnectSocket(): void {
  if (socket) {
    socket.disconnect();
    socket = null;
  }
}

/**
 * Join a post room for real-time updates
 */
export function joinPostRoom(postId: string): void {
  socket?.emit('join:post', { postId });
}

/**
 * Leave a post room
 */
export function leavePostRoom(postId: string): void {
  socket?.emit('leave:post', { postId });
}

/**
 * Like/Unlike a post via WebSocket
 */
export function likePost(postId: string): void {
  socket?.emit('post:like', { postId });
}

/**
 * Create a comment via WebSocket
 */
export function createComment(
  postId: string,
  content: string,
  parentId?: string,
  mentions?: string[]
): void {
  socket?.emit('comment:create', { postId, content, parentId, mentions });
}

/**
 * Like/Unlike a comment via WebSocket
 */
export function likeComment(commentId: string, postId: string): void {
  socket?.emit('comment:like', { commentId, postId });
}

/**
 * Vote on a poll via WebSocket
 */
export function votePoll(postId: string, optionId: string): void {
  socket?.emit('poll:vote', { postId, optionId });
}

/**
 * Update location via WebSocket
 */
export function updateLocation(data: {
  lat?: number;
  lng?: number;
  accuracy?: number;
  activity?: string;
}): void {
  socket?.emit('location:update', data);
}

/**
 * Send typing indicator (for future use)
 */
export function sendTypingIndicator(postId: string, isTyping: boolean): void {
  socket?.emit('typing', { postId, isTyping });
}

export default {
  initializeSocket,
  getSocket,
  disconnectSocket,
  joinPostRoom,
  leavePostRoom,
  likePost,
  createComment,
  likeComment,
  votePoll,
  updateLocation,
  sendTypingIndicator,
};
